import React from 'react';
import { Block, Controller, RenderNodeProps } from '@ali/4ever-cangjie';
import { VoteCardPlugin } from '@ali/4ever-bamboo';

type VotePluginConfig = VoteCardPlugin.VotePluginConfig;

interface IProps {
  nodeProps: RenderNodeProps<Block>;
  controller: Controller;
  voteConfig: VotePluginConfig;
}

export const Vote: React.FC<IProps> = React.memo(({
  nodeProps,
  controller,
  voteConfig,
}) => {
  const {
    locale,
    getVote,
    onSocketMsg
  } = voteConfig;

  const { node, isSelected } = nodeProps;
  const { value: { selection } } = controller;

  return (
    <VoteCardPlugin.Container
      locale={locale}
      node={node}
      getVote={getVote}
      controller={controller}
      isSelected={isSelected && selection.isCollapsed}
      onSocketMsg={onSocketMsg}
    />
  );
});
